import {
	mapState
} from 'vuex'
import company from './company.vue'
import admin from './admin.vue'
export default {
	components: {
		company,
		admin
	},
	computed: mapState(['hasLogin', 'username', 'userInfo']),
	onLoad() {
		console.log(this.userInfo)
		if (!this.hasLogin) {
			uni.showModal({
				title: '未登录',
				content: '您未登录，需要登录后才能继续',
				showCancel: false,
				success: (res) => {
					if (res.confirm) {
						uni.navigateTo({
							url: '../login/login'
						});
					}
				}
			});
		}
	}
}
